<template>
    <div>
         <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
        <el-row>
            <el-col :span="8">
                <el-input placeholder="">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
                  
            </el-col>
        </el-row>
        <el-table :data="orderList" border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="order_number"></el-table-column>
            <el-table-column label="" prop="order_price"></el-table-column>
            <el-table-column label="" prop="order_pay">
                <template slot-scope="scope">
                    <el-tag type="warning" v-if="scope.row.order_pay ===0"></el-tag>
                    <el-tag type="success" v-else></el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" prop="is_send"></el-table-column>
            <el-table-column label="" prop="create_time|DateFormat">
                <template slot-scope="scope">
                        {{scope.row.create_time| DateFormat}}
                    </template>
            </el-table-column>
            <el-table-column label="">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog"></el-button>
                <el-button type="success" icon="el-icon-s-help" size="mini" @click="showProcessdialog"></el-button>
            </el-table-column>
        </el-table>
         <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagesize"
            :page-sizes="[5, 10, 15, 17]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
    </el-pagination>
    </el-card>
    <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="30%"
        @close ="closedDialog">
        <el-form :model="editForm" :rules="editRules" ref="editFormRef" label-width="100px" >
            <el-form-item label="/" prop="address1">
                <el-cascader :options="cityData" v-model="editForm.address1"></el-cascader>
            </el-form-item>
             <el-form-item label="" prop="address2">
                    <el-input></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editDialogVisible = false"> </el-button>
        </span>
    </el-dialog>
    <el-dialog
        title=""
        :visible.sync="ProcessdialogVisible"
        width="50%"
        >
        <el-timeline>
            <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :timestamp="activity.time">
            {{activity.context}}
            </el-timeline-item>
        </el-timeline>
        
    </el-dialog>
    </div>
</template>
<script>
import cityData from './citydata.js'
export default {
    data() {
        return {
            queryInfo:{
                query:'',
                pagenum:1,
                pagesize:10
            },
            total:0,
            orderList:[],
            editDialogVisible:false,
            editForm:{
                address1:[],
                address2:'',
            },
            editRules:{
            address1:[{required: true,message:'/'
            ,trigger:'blur'}],
            address2:[{required: true,message:''
            ,trigger:'blur'}],
            },
            cityData,
            ProcessdialogVisible:false,
            progressInfo:[]

        }
    },
    created() {
        this.getOrdersList()
    },
    methods: {
       async getOrdersList(){
           const {data:res} = await this.$http.get('orders',{params:this.queryInfo})
           if (res.meta.status!==200) {
               return this.$message.error("")
           }
           this.$message.success("")
           this.total = res.data.total;
           this.orderList = res.data.goods
       },
       handleSizeChange(newSize){
           this.queryInfo.pagesize = newSize;
           this.getOrdersList()
       },
       handleCurrentChange(newPage){
           this.queryInfo.pagenum = newPage;
           this.getOrdersList()
       },
       showEditDialog(){
           this.editDialogVisible  = true
       },
       closedDialog(){
           this.$refs.editFormRef.resetFields()
       },
       async showProcessdialog(){
        //    const {data:res} = await this.$http.get('/kuaidi/1106975712662')
        //    if (res.meta.status!==200) {
        //        return this.$message.error("")
        //    }
        //    this.processInfo = res.data
         this.progressInfo = [
        {
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: ''
        },
        {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context: '[]  95338',
          location: ''
        },
        {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: ' []',
          location: ''
        },
        {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context: '[], []',
          location: ''
        },
        {
          time: '2018-05-09 23:05:00',
          ftime: '2018-05-09 23:05:00',
          context: ' []',
          location: ''
        },
        {
          time: '2018-05-09 21:21:00',
          ftime: '2018-05-09 21:21:00',
          context: '[], []',
          location: ''
        },
        {
          time: '2018-05-09 13:07:00',
          ftime: '2018-05-09 13:07:00',
          context: ' ',
          location: ''
        },
        {
          time: '2018-05-09 12:25:03',
          ftime: '2018-05-09 12:25:03',
          context: '',
          location: ''
        },
        {
          time: '2018-05-09 12:22:24',
          ftime: '2018-05-09 12:22:24',
          context: 'HLA',
          location: ''
        },
        {
          time: '2018-05-08 21:36:04',
          ftime: '2018-05-08 21:36:04',
          context: '',
          location: ''
        }
      ]
           this.ProcessdialogVisible = true;
       }
    },
}
</script>
<style lang="less" scoped>
.el-cascader{
    width: 100%;
}
</style>